const hamburger = document.querySelector('.hamburger');
const menuElements = document.querySelectorAll('.menu__element');
const menuList = document.querySelector('.menu__list');


hamburger.addEventListener('click', (e)=>{
    e.target.closest('.hamburger').classList.toggle('hamburger--active');
    e.target.closest('.menu').querySelector('.menu__list').classList.toggle('menu__list--active');
});

for(let i = 0; i<menuElements.length; i++){
    menuElements[i].addEventListener('click', closeMenu);
}

function closeMenu(){
    hamburger.classList.remove('hamburger--active');
    menuList.classList.remove('menu__list--active');
    console.log(hamburger, menuList);
}